@import "/src/Global";

* {
  margin: 0;
  padding: 0;
}

.header, footer {
  width: 100%;
}

 .App {
   min-height: 100vh;
   display: flex;
   flex-direction: column;
 }

.main {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 152px;
  padding-right: 152px;
  margin-bottom: 80px;
  z-index: 1;

  @include desktop-style {
    padding-left: 32px;
    padding-right: 32px;
  }

  @include tablet-style {
    padding-left: 24px;
    padding-right: 24px;
    margin-bottom: 64px;
  }

  @include phone-style {
    margin-bottom: 56px;
    padding-left: 16px;
    margin-right: 16px;
  }

  &--with-burger {
    display: none;
  }
}

img {
  transition: transform 0.3s ease;
}

img:hover {
  transform: scale(1.1); 
}

button {
  appearance: none; 
  background-color: transparent;
  border: none; 
  padding: 0;
  margin: 0; 
  font-family: inherit; 
  font-size: inherit; 
  cursor: pointer;
  outline: none; 
}
